.product {
  position: relative;
  overflow: hidden;
  background: #1d1d1d;
  color: #fff; }
  .product__image-container {
    width: 100%;
    height: 400px;
    overflow: hidden; }
    .product__image-container__img {
      width: 100%;
      height: 100%;
      object-fit: cover;
      -webkit-transition: .5s;
      transition: .5s;
      z-index: 2;
      position: relative; }
  .product__info {
    margin: 2rem; }
    .product__info__back {
      position: absolute;
      opacity: 0.4;
      top: 27px;
      left: 25px;
      -webkit-filter: grayscale(0.5) blur(2px); }
    .product__info__title {
      position: relative;
      z-index: 2; }
  .product:nth-of-type(2n) .row {
    color: #fff; }
    .product:nth-of-type(2n) .row > div:first-child {
      order: 2; }
    .product:nth-of-type(2n) .row > div:second-child {
      order: 1; }
  .product:hover .product__image-container__img {
    -webkit-transform: scale(1.1);
            transform: scale(1.1); }

